<template>
  <div class="video_box_s">
    <div class="video_box">
      <div class="audiotitlex">
        <div class="img_box">
          <img class="img_box" src="@/assets/icon/shipin.png" />
        </div>
        <div class="fileName_box">{{ fileName }}</div>
      </div>
      <div class="video_con" @contextmenu.prevent="menuPlayer()">
        <video-player
          v-if="goodsFilePath"
          class="video-player vjs-custom-skin"
          ref="videoPlayer"
          :playsinline="true"
          :options="playerOptions"
        ></video-player>
      </div>
    </div>
  </div>
</template>
<script>
import { videoPlayer } from "vue-video-player";
import "video.js/dist/video-js.css";
export default {
  components: {
    videoPlayer,
  },
  computed: {
    playerOptions() {
      // 
      const playerOptionsObj = {
        playbackRates: [0.5, 0.8, 1.0, 1.5, 2.0, 3.0, 5.0], //
        autoplay: false, // true,
        muted: false, // 
        loop: false, // 
        preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: false, // trueVideo.js player
        sources: [
          {
            type: "video/" + this.fileType, // 'video/mp4'notSupportedMessage
            src: this.goodsFilePath, // url
          },
        ],
        poster: "", // 
        notSupportedMessage: "...", // Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true, //
        },
      };
      return playerOptionsObj;
    },
  },
  data() {
    return {
      fileName: "",
      goodsFilePath: "", //()
      loading: false,
      fileType: "mp4",
    };
  },
  created() {
    $(document).ready(function () {
      $("#video").bind("contextmenu", function () {
        return false;
      });
    });
  },
  methods: {
    tabbook(item, index, filePath) {
      this.loading = this.$loading({
        lock: true,
      });
      this.fileName = item.sourceName;
      this.goodsFilePath = filePath;
      this.loading.close();
    },
  },
};
</script>

<style scoped>
.video_box_s {
  overflow: hidden;
}
.video_box {
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  overflow: hidden;
  background: #fffefd;
  min-height: 95vh;
}

.video_boxt {
  width: 100%;
  float: left;
  font-size: 14px;
  padding: 0 20px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #666666;
  line-height: 41px;
  margin-bottom: 30px;
  height: 41px;
  border-bottom: 1px solid #f2f2f2;
}

.video_con {
  width: 100%;
  height: 90vh;
  margin: 0 auto;
  text-align: center;
}
.course_node_video ::v-deep .vjs-progress-control {
  pointer-events: none;
}
::v-deep .vjs-time-tooltip {
  width: 50px;
}
/* //video */
video::-internal-media-controls-download-button {
  display: none;
}

video::-webkit-media-controls-enclosure {
  overflow: hidden;
}

video::-webkit-media-controls-panel {
  width: calc(100% + 30px);
}
/* //audio */
audio::-internal-media-controls-download-button {
  display: none;
}
audio::-webkit-media-controls-enclosure {
  overflow: hidden;
}
audio::-webkit-media-controls-panel {
  width: calc(100% + 35px);
}
::v-deep .vjs-big-play-button {
  left: 50%;
  top: 50%;
  margin-left: -45px;
  margin-top: -22.5px;
}
.img_box {
  width: 20px;
  height: 20px;
  background: #666666;
}
.audiotitlex {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 5px 15px;
  border-bottom: 1px solid #f2f2f2;
  padding: 5px;
}
.fileName_box {
  margin-left: 10px;
  font-size: 14px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #666666;
  line-height: 26px;
}
</style>
